
.sidebar{
  width: 250px;
  height: 100vh;
  background: white;
  top: 0;
  left: 0;
  transition: all 0.5s;
  -ms-transform: translateX(-100%);
      transform: translateX(-100%);
}
.sidebar__close-btn{
  float: right;
  height: 44px;
  width: 44px;
  margin: 10px 10px 0 0;
}
.sidebar__content{
  margin-left: 35px;
}
.sidebar__logo{
  margin: 125px 0 80px;
}
.sidebar__item{
  margin-bottom: 23px;
}
.sidebar__item span{
  margin-left: 10px;
}
.sidebar__item img{
  vertical-align: middle;
}



